<template>
  <div class="all">
    <mu-paper :z-depth="1" class="demo-list-wrap">
      <div class="demo-text" style="font-weight:700;color:#000">
        <p>SHOW涨跌幅监控报警</p>
      </div>
      <mu-list>
        <mu-sub-header>提醒条件</mu-sub-header>
        <div class="main">
          <div>
            监控时间段:
            <span>{{value1}}</span> min内
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value1,time1)"
            />
          </div>
        </div>
        <div class="main">
          <div>
            涨幅报警范围:
            <span>{{value6}}</span> %
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value6,up1)"
            />
          </div>
        </div>
        <div class="main">
          <div>
            跌幅报警范围:
            <span>{{value3}}</span> %
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value3,down1)"
            />
          </div>
        </div>

        <div class="txt" >
					监控一段时间内SHOW涨跌幅变化并进行电话和短信报警。涨跌幅监控规则如下：任意时刻的一个成交单的成交价和规定时间段内全部成交单（如规定时间段内内没有成交单，则和上一个已成交单的成交价做比较）中的最大值和最小值做对比（如标的成交单的成交价是规定时间段内的最大值，则和规定时间段内最小值做比较，如标的成交单的成交价是最小值，则和规定时间段内最大值做比较），如对比结果是有且只有一个差值振幅达到规定范围，则报警，如两个结果都达到报警范围，则只取距离标的成交单时间上最近的一个对比单的差值振幅数据进行报警。
			</div>
      </mu-list>
      <mu-dialog
        title
        width="600"
        max-width="80%"
        :esc-press-close="false"
        :overlay-close="false"
        :open.sync="openAlert"
      >
        {{name_label}}
        <!-- 一直涨设置提醒阈值 -->
        <mu-text-field type="number" v-model="value2" placeholder></mu-text-field>
        <mu-button slot="actions" flat color="primary" @click="cencle(value2)">取消</mu-button>
        <mu-button slot="actions" flat color="primary" @click="save(value2)">保存设置</mu-button>
      </mu-dialog>
    </mu-paper>
  </div>
</template>

<script>
export default {
  data() {
    return {
      up:'',
      down:'',
      up1:'up',
      down1:'down',
      name_label: "",
      active1: 0,
      disabled: true,
      openAlert: false,
      readonly: true,
      value1: "",
      value2: "",
      value3: "",
      value4: "",
      value5: "",
      value6: "",
      value7: "",
      value8: "",
      value1_out: "",
      value2_out: "",
      value3_out: "",
      value5_out: "",
      value6_out: "",
      value8_out: "",
      amount: "amount",
      quantity: "quantity",
      amount_add: "amount_add",
      quantity_add: "quantity_add",
      time: "",
      time1: "time",
      flag: "",
      visibility: false,
      events: false,
      calls: false,
      messages: false,
      notifications: false,
      sounds: false,
      videoSounds: false
    };
  },
  mounted() {
    this.get_fetch();
  },
  methods: {
    tab(active1) {
      console.log(this.active1);
      this.get_fetch();
    },
    openAlertDialog(q, t) {
      console.log(q + "q-----------------------");
      console.log(t + "t-----------------------");
      //q = this.value2;
      if(t == "time"){
        this.name_label = "监控时间段:"
      }else if(t == "up"){
        this.name_label = "涨幅报警范围:"
      }else if(t == "down"){
        this.name_label = "跌幅报警范围:"
      }
      this.value2 = "";
      this.value2_out = "";
      this.flag = t;
      this.openAlert = true;
    },
    cencle(e) {
      console.log("取消");
      this.openAlert = false;
    },
    save(q1) {
      console.log(this.flag)
      if (this.flag == "time") {
        console.log("time");
        var push_data = {
            time: '00:'+this.value2, //时间
            up: this.value6, //涨
            down: this.value3 //跌
        };
      } else if (this.flag == "up") {
        console.log("up");
        var push_data = {
            time: '00:'+this.value1, //时间
            up: this.value2, //涨
            down: this.value3 //跌
        };
      } else if (this.flag == "down") {
        console.log("down");
        var push_data = {
            time: '00:'+this.value1, //时间
            up: this.value6, //涨
            down: this.value2 //跌
        };
      }
      this.fetch(push_data);
      this.openAlert = false;
    },
    fetch(push_data) {
      console.log(push_data);
      var that = this;
      this.$http
        .post(
          "http://trendapi.show-fund.com/update_alarm_config/",
          push_data
        )
        .then(function(res) {
          console.log(res);
            that.get_fetch();
        });
    },
    get_fetch() {
      var that = this;
      this.$http
        .get(
          "http://trendapi.show-fund.com/get_alarm_config/"
        )
        .then(function(res) {
          console.log(res.data)
          that.value1 = res.data.time.substring(2); //时间
          that.value6 = res.data.up; //涨
          that.value3 = res.data.down; //跌
        });
    }
  }
};
</script>
<style scoped>
.txt{
    text-align: left;
    margin-top: 20px;
    font-size: 16px;
    padding: 15px;
}
.all {
    font-size: 20px;
}
.main {
  padding: 0px 16px;
  text-align: left;
}
.demo-list-wrap {
  width: 100%;
  overflow: hidden;
}
</style>



