        <mu-button  v-if="active1==1" slot="actions" flat color="primary" @click="cencle(value2_out)">取消</mu-button>
        <mu-button  v-if="active1==1" slot="actions" flat color="primary" @click="save(value2_out)">保存设置</mu-button>
      </mu-dialog>
    </mu-paper>
  </div>
