<script>
export default {
  data() {
    return {
      name_label: "",
      active1: 0,
      disabled: true,
      openAlert: false,
      readonly: true,
      value1: "",
      value2: "",
      value3: "",
      value4: "",
      value5: "",
      value6: "",
      value7: "",
      value8: "",
      value1_out: "",
      value2_out: "",
      value3_out: "",
      value5_out: "",
      value6_out: "",
      value8_out: "",
      amount: "amount",
      volume: "volume",
      increase_amount: "increase_amount",
      increase_volume: "increase_volume",
      time_range: "time_range",
      flag: "",
      visibility: false,
      events: false,
      calls: false,
      messages: false,
      notifications: false,
      sounds: false,
      videoSounds: false
    };
  },
  mounted() {
    this.get_fetch();
  },
  methods: {
    tab(active1){
    console.log(this.active1)
     this.get_fetch()
    },
    openAlertDialog(q, t) {
      console.log(q + "q-----------------------");
      console.log(t + "t-----------------------");
      //q = this.value2;
      if(this.active1 ==1){
        if(t == "amount"){
        this.name_label = "累计提现金额大于等于："
      }else if(t == "volume"){
        this.name_label = "累计提现数量大于等于："
      }else if(t == "increase_amount"){
        this.name_label = "金额每增加："
      }else if(t == "increase_volume"){
        this.name_label = "数量每增加："
      }else if(t == "time_range"){
        this.name_label = "监控时间范围："
      }
      }else if(this.active1 ==0){
        if(t == "amount"){
        this.name_label = "累计充值金额大于等于："
      }else if(t == "volume"){
        this.name_label = "累计充值数量大于等于："
      }else if(t == "increase_amount"){
        this.name_label = "金额每增加："
      }else if(t == "increase_volume"){
        this.name_label = "数量每增加："
      }else if(t == "time_range"){
        this.name_label = "监控时间范围："
      }
      }

      this.value2 = "";
      this.value2_out = "";
      this.flag = t;
      this.openAlert = true;
    },
    cencle(e) {
      console.log("取消");
      this.openAlert = false;
    },
    save(q1) {
      if (this.flag == "amount" && this.active1 == 0) {
        console.log("amount");
        var push_data = {
          name: "show_chain_ex_depoist_alarm",
          data: {
            amount: this.value2, //充值金额
            volume: this.value6, //充值数量
            increase_amount: this.value5, //金额增加
            increase_volume: this.value3, //数量增加
            time_range: this.value8 //监控时间范围
          }
        };
      } else if (this.flag == "volume" && this.active1 == 0) {
        console.log("volume");
        var push_data = {
          name: "show_chain_ex_depoist_alarm",
          data: {
            amount: this.value1, //充值金额
            volume: this.value2, //充值数量
            increase_amount: this.value5, //金额增加
            increase_volume: this.value3, //数量增加
            time_range: this.value8 //监控时间范围
          }
        };
      } else if (this.flag == "increase_amount" && this.active1 == 0) {
        console.log("increase_amount");
        var push_data = {
          name: "show_chain_ex_depoist_alarm",
          data: {
            amount: this.value1, //充值金额
            volume: this.value6, //充值数量
            increase_amount: this.value2, //金额增加
            increase_volume: this.value3, //数量增加
            time_range: this.value8 //监控时间范围
          }
        };
      } else if (this.flag == "increase_volume" && this.active1 == 0) {
        console.log("increase_volume");
        var push_data = {
          name: "show_chain_ex_depoist_alarm",
          data: {
            amount: this.value1, //充值金额
            volume: this.value6, //充值数量
            increase_amount: this.value5, //金额增加
            increase_volume: this.value2, //数量增加
            time_range: this.value8 //监控时间范围
          }
        };
      } else if (this.flag == "time_range" && this.active1 == 0) {
        console.log("time_range");
        var push_data = {
          name: "show_chain_ex_depoist_alarm",
          data: {
            amount: this.value1, //充值金额
            volume: this.value6, //充值数量
            increase_amount: this.value5, //金额增加
            increase_volume: this.value3, //数量增加
            time_range: this.value2 //监控时间范围
          }
        };
      }
      else if (this.flag == "amount" && this.active1 == 1) {
        console.log("amount");
        var push_data_out = {
          name: "show_chain_ex_withdraw_alarm",
          data: {
            amount: this.value2_out, //充值金额
            volume: this.value6_out, //充值数量
            increase_amount: this.value5_out, //金额增加
            increase_volume: this.value3_out, //数量增加
            time_range: this.value8_out //监控时间范围
          }
        };
      } else if (this.flag == "volume" && this.active1 == 1) {
        console.log("volume");
        var push_data_out = {
          name: "show_chain_ex_withdraw_alarm",
          data: {
            amount: this.value1_out, //充值金额
            volume: this.value2_out, //充值数量
            increase_amount: this.value5_out, //金额增加
            increase_volume: this.value3_out, //数量增加
            time_range: this.value8_out //监控时间范围
          }
        };
      } else if (this.flag == "increase_amount" && this.active1 == 1) {
        console.log("increase_amount");
        var push_data_out = {
          name: "show_chain_ex_withdraw_alarm",
          data: {
            amount: this.value1_out, //充值金额
            volume: this.value6_out, //充值数量
            increase_amount: this.value2_out, //金额增加
            increase_volume: this.value3_out, //数量增加
            time_range: this.value8_out //监控时间范围
          }
        };
      } else if (this.flag == "increase_volume" && this.active1 == 1) {
        console.log("increase_volume");
        var push_data_out = {
          name: "show_chain_ex_withdraw_alarm",
          data: {
            amount: this.value1_out, //充值金额
            volume: this.value6_out, //充值数量
            increase_amount: this.value5_out, //金额增加
            increase_volume: this.value2_out, //数量增加
            time_range: this.value8_out //监控时间范围
          }
        };
      } else if (this.flag == "time_range" && this.active1 == 1) {
        console.log("time_range");
        var push_data_out = {
          name: "show_chain_ex_withdraw_alarm",
          data: {
            amount: this.value1_out, //充值金额
            volume: this.value6_out, //充值数量
            increase_amount: this.value5_out, //金额增加
            increase_volume: this.value3_out, //数量增加
            time_range: this.value2_out //监控时间范围
          }
        };
      }
      if(this.active1 == 0){
        this.fetch(push_data);
        this.openAlert = false;
      }else if(this.active1 == 1){
        this.fetch(push_data_out);
        this.openAlert = false;
      }
    },
    fetch(push_data) {
      console.log(push_data);
      var that = this;
      this.$http
        .post(
          "http://trendapi.show-fund.com/update_app_common_config/",
          push_data
        )
        .then(function(res) {
          console.log(res);
          if (res.statusText == "OK") {
            that.get_fetch();
          }
        });
    },
    get_fetch() {
      var that = this;
      if(this.active1 == 0){
        this.$http
        .get(
          "http://trendapi.show-fund.com/get_app_common_config/show_chain_ex_depoist_alarm/"
        )
        .then(function(res) {
            that.value1 = res.data.data.amount//充值金额
            that.value6 = res.data.data.volume //充值数量
            that.value5 = res.data.data.increase_amount//金额增加
            that.value3 = res.data.data.increase_volume//数量增加
            that.value8 = res.data.data.time_range; //监控时间范围
        });
      }else if(this.active1 == 1){
        console.log("初始化进1了")
        this.$http
        .get(
          "http://trendapi.show-fund.com/get_app_common_config/show_chain_ex_withdraw_alarm/"
        )
        .then(function(res) {
            that.value1_out = res.data.data.amount//充值金额
            that.value6_out  = res.data.data.volume //充值数量
            that.value5_out  = res.data.data.increase_amount//金额增加
            that.value3_out  = res.data.data.increase_volume//数量增加
            that.value8_out  = res.data.data.time_range; //监控时间范围
        });
      }

