import 'muse-ui/dist/muse-ui.css';
import axios from 'axios'
import VueAxios from 'vue-axios'
import rem from './assets/rem'
import 'lib-flexible/flexible.js'
Vue.use(rem)
Vue.use(VueAxios, axios)
Vue.use(Mint);
Vue.use(MuseUI);
// Vue.use(Mint);
