import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import cointype from '@/components/cointype'
import cointype1 from '@/components/cointype1'
import cointype2 from '@/components/cointype2'
import up_down from '@/components/up_down'
import phone from '@/components/phone'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/cointype',
      name: 'cointype',
      component: cointype
    },
    {
      path: '/cointype1',
      name: 'cointype1',
      component: cointype1
    },
    {
      path: '/cointype2',
      name: 'cointype2',
      component: cointype2
    },
    {
      path: '/phone',
      name: 'phone',
      component: phone
    },
    {
      path: '/up_down',
      name: 'up_down',
      component: up_down
    }
  ]
})



