            />
          </div>
          <div>
            累计挂单数量大于等于:
            <span>{{value6| NumFormat}}</span> SHOW
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value6,quantity)"
            />
          </div>
          <div>
            累计挂单金额大于等于:
            <span>{{value1| NumFormat}}</span> USDT
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value1,amount)"
            />
          </div>
        </div>
        <mu-sub-header>再次提醒条件</mu-sub-header>
        <div class="main">
          <div>
            数量每增加:
            <span>{{value3| NumFormat}}</span> SHOW
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value3,quantity_add)"
            />
          </div>
          <div>
            金额每增加:
            <span>{{value5| NumFormat}}</span> USDT
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value5,amount_add)"
            />
          </div>
        </div>
        <!-- <div class="super" style="margin-top:1rem">
          <mu-list-item button :ripple="false" @click="events = !events">
            <mu-list-item-title>邮件提醒</mu-list-item-title>
            <mu-list-item-action>
              <mu-switch v-model="events"  disabled></mu-switch>
            </mu-list-item-action>
          </mu-list-item>
          <mu-list-item button :ripple="false" @click="calls = !calls">
            <mu-list-item-title>电话提醒</mu-list-item-title>
            <mu-list-item-action>
              <mu-switch v-model="calls" disabled></mu-switch>
            </mu-list-item-action>
          </mu-list-item>
          <mu-list-item button :ripple="false" @click="messages = !messages">
            <mu-list-item-title>信息提醒</mu-list-item-title>
            <mu-list-item-action>
              <mu-switch v-model="messages" disabled></mu-switch>
            </mu-list-item-action>
          </mu-list-item>
        </div>-->
      </mu-list>

      <mu-list v-if="active1 === 1">
        <mu-sub-header>首次提醒条件</mu-sub-header>
        <div class="main">
          <div>
            卖一价上浮:
            <span>{{value8_out}}</span> %
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value8_out,time)"
            />
          </div>
          <div>
            累计挂单数量大于等于:
            <span>{{value6_out| NumFormat}}</span> SHOW
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value6_out,quantity)"
            />
          </div>
          <div>
            累计挂单金额大于等于:
            <span>{{value1_out| NumFormat}}</span> USDT
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value1_out,amount)"
            />
          </div>
        </div>
        <mu-sub-header>再次提醒条件</mu-sub-header>
        <div class="main">
          <div>
            数量每增加:
            <span>{{value3_out| NumFormat}}</span> SHOW
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value3_out,quantity_add)"
            />
          </div>
          <div>
            金额每增加:
            <span>{{value5_out| NumFormat}}</span> USDT
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value5_out,amount_add)"
            />
          </div>
        </div>
      </mu-list>
      <mu-dialog
        title
        width="600"
        max-width="80%"
        :esc-press-close="false"
        :overlay-close="false"
        :open.sync="openAlert"
      >
        {{name_label}}
        <!-- 一直涨设置提醒阈值 -->
        <mu-text-field type="number" v-if="active1==0" v-model="value2" placeholder></mu-text-field>
        <mu-text-field type="number" v-if="active1==1" v-model="value2_out" placeholder></mu-text-field>
        <mu-button v-if="active1==0" slot="actions" flat color="primary" @click="cencle(value2)">取消</mu-button>
        <mu-button v-if="active1==0" slot="actions" flat color="primary" @click="save(value2)">保存设置</mu-button>
        <mu-button
          v-if="active1==1"
          slot="actions"
          flat
          color="primary"
          @click="cencle(value2_out)"
        >取消</mu-button>
        <mu-button
          v-if="active1==1"
          slot="actions"
          flat
          color="primary"
          @click="save(value2_out)"
        >保存设置</mu-button>
      </mu-dialog>
    </mu-paper>
  </div>
