<script>
export default {
  data() {
    return {
      name_label: "",
      active1: 1,
      disabled: true,
      openAlert: false,
      readonly: true,
      value1: "",
      value2: "",
      value3: "",
      value4: "",
      value5: "",
      value6: "",
      value7: "",
      value8: "",
      value1_out: "",
      value2_out: "",
      value3_out: "",
      value5_out: "",
      value6_out: "",
      value8_out: "",
      amount: "amount",
      quantity: "quantity",
      amount_add: "amount_add",
      quantity_add: "quantity_add",
      time: "time",
      flag: "",
      visibility: false,
      events: false,
      calls: false,
      messages: false,
      notifications: false,
      sounds: false,
      videoSounds: false
    };
  },
  mounted() {
    this.get_fetch();
  },
  methods: {
    tab(active1) {
      console.log(this.active1);
      this.get_fetch();
    },
    openAlertDialog(q, t) {
      console.log(q + "q-----------------------");
      console.log(t + "t-----------------------");
      //q = this.value2;
      if (this.active1 == 0) {
        if (t == "amount") {
          this.name_label = "累计挂单金额大于等于:";
        } else if (t == "quantity") {
          this.name_label = "累计挂单数量大于等于:";
        } else if (t == "amount_add") {
          this.name_label = "金额每增加:";
        } else if (t == "quantity_add") {
          this.name_label = "数量每增加:";
        } else if (t == "time") {
          this.name_label = "买一价下浮:";
        }
      } else if (this.active1 == 1) {
        if (t == "amount") {
          this.name_label = "累计挂单金额大于等于:";
        } else if (t == "quantity") {
          this.name_label = "累计挂单数量大于等于:";
        } else if (t == "amount_add") {
          this.name_label = "金额每增加:";
        } else if (t == "quantity_add") {
          this.name_label = "数量每增加:";
        } else if (t == "time") {
          this.name_label = "卖一价上浮:";
        }
      }

      this.value2 = "";
      this.value2_out = "";
      this.flag = t;
      this.openAlert = true;
    },
    cencle(e) {
      console.log("取消");
      this.openAlert = false;
    },
    save(q1) {
      if (this.flag == "amount" && this.active1 == 0) {
        console.log("amount");
        var push_data = {
          name: "big_amount_quantity_buy",
          data: {
            amount: this.value2, //充值金额
            quantity: this.value6, //充值数量
            amount_add: this.value5, //金额增加
            quantity_add: this.value3, //数量增加
            percent: this.value8 //监控时间范围
          }
        };
      } else if (this.flag == "quantity" && this.active1 == 0) {
        console.log("quantity");
        var push_data = {
          name: "big_amount_quantity_buy",
          data: {
            amount: this.value1, //充值金额
            quantity: this.value2, //充值数量
            amount_add: this.value5, //金额增加
            quantity_add: this.value3, //数量增加
            percent: this.value8 //监控时间范围
          }
        };
      } else if (this.flag == "amount_add" && this.active1 == 0) {
        console.log("amount_add");
        var push_data = {
          name: "big_amount_quantity_buy",
          data: {
            amount: this.value1, //充值金额
            quantity: this.value6, //充值数量
            amount_add: this.value2, //金额增加
            quantity_add: this.value3, //数量增加
            percent: this.value8 //监控时间范围
          }
        };
      } else if (this.flag == "quantity_add" && this.active1 == 0) {
        console.log("quantity_add");
        var push_data = {
          name: "big_amount_quantity_buy",
          data: {
            amount: this.value1, //充值金额
            quantity: this.value6, //充值数量
            amount_add: this.value5, //金额增加
            quantity_add: this.value2, //数量增加
            percent: this.value8 //监控时间范围
          }
        };
      } else if (this.flag == "time" && this.active1 == 0) {
        console.log("time");
        var push_data = {
          name: "big_amount_quantity_buy",
          data: {
            amount: this.value1, //充值金额
            quantity: this.value6, //充值数量
            amount_add: this.value5, //金额增加
            quantity_add: this.value3, //数量增加
            percent: this.value2 //监控时间范围
          }
        };
      } else if (this.flag == "amount" && this.active1 == 1) {
        console.log("amount");
        var push_data_out = {
          name: "big_amount_quantity_sell",
          data: {
            amount: this.value2_out, //充值金额
            quantity: this.value6_out, //充值数量
            amount_add: this.value5_out, //金额增加
            quantity_add: this.value3_out, //数量增加
            percent: this.value8_out //监控时间范围
          }
        };
      } else if (this.flag == "quantity" && this.active1 == 1) {
        console.log("quantity");
        var push_data_out = {
          name: "big_amount_quantity_sell",
          data: {
            amount: this.value1_out, //充值金额
            quantity: this.value2_out, //充值数量
            amount_add: this.value5_out, //金额增加
            quantity_add: this.value3_out, //数量增加
            percent: this.value8_out //监控时间范围
          }
        };
      } else if (this.flag == "amount_add" && this.active1 == 1) {
        console.log("amount_add");
        var push_data_out = {
          name: "big_amount_quantity_sell",
          data: {
            amount: this.value1_out, //充值金额
            quantity: this.value6_out, //充值数量
            amount_add: this.value2_out, //金额增加
            quantity_add: this.value3_out, //数量增加
            percent: this.value8_out //监控时间范围
          }
        };
      } else if (this.flag == "quantity_add" && this.active1 == 1) {
        console.log("quantity_add");
        var push_data_out = {
          name: "big_amount_quantity_sell",
          data: {
            amount: this.value1_out, //充值金额
            quantity: this.value6_out, //充值数量
            amount_add: this.value5_out, //金额增加
            quantity_add: this.value2_out, //数量增加
            percent: this.value8_out //监控时间范围
          }
        };
      } else if (this.flag == "time" && this.active1 == 1) {
        console.log("time");
        var push_data_out = {
          name: "big_amount_quantity_sell",
          data: {
            amount: this.value1_out, //充值金额
            quantity: this.value6_out, //充值数量
            amount_add: this.value5_out, //金额增加
            quantity_add: this.value3_out, //数量增加
            percent: this.value2_out //监控时间范围
          }
        };
      }
      if (this.active1 == 0) {
        this.fetch(push_data);
        this.openAlert = false;
      } else if (this.active1 == 1) {
        this.fetch(push_data_out);
        this.openAlert = false;
      }
    },
    fetch(push_data) {
      console.log(push_data);
      var that = this;
      this.$http
        .post(
          "http://trendapi.show-fund.com/update_app_common_config/",
          push_data
        )
        .then(function(res) {
          console.log(res);
          if (res.statusText == "OK") {
            that.get_fetch();
          }
        });
    },
    get_fetch() {
      var that = this;
      if (this.active1 == 0) {
        this.$http
          .get(
            "http://trendapi.show-fund.com/get_app_common_config/big_amount_quantity_buy/"
          )
          .then(function(res) {
            that.value1 = res.data.data.amount; //充值金额
            that.value6 = res.data.data.quantity; //充值数量
            that.value5 = res.data.data.amount_add; //金额增加
            that.value3 = res.data.data.quantity_add; //数量增加
            that.value8 = res.data.data.percent; //监控时间范围
          });
      } else if (this.active1 == 1) {
        console.log("初始化进1了");
        this.$http
          .get(
            "http://trendapi.show-fund.com/get_app_common_config/big_amount_quantity_sell/"
          )
          .then(function(res) {
            that.value1_out = res.data.data.amount; //充值金额
            that.value6_out = res.data.data.quantity; //充值数量
            that.value5_out = res.data.data.amount_add; //金额增加
            that.value3_out = res.data.data.quantity_add; //数量增加
            that.value8_out = res.data.data.percent; //监控时间范围
          });
      }
    }
  }
};
</script>
<style scoped>
.all {
    font-size: 17px;
}
.main {
  padding: 0px 16px;
  text-align: left;
}
.demo-list-wrap {
  width: 100%;
  overflow: hidden;
}
</style>



