<template>
  <div class="all">
    <mu-paper :z-depth="1" class="demo-list-wrap">
      <div class="demo-text" style="font-weight:700;color:#000">
        <p>SHOW链上大额转账报警</p>
      </div>
      <mu-list>
        <mu-sub-header>提醒条件</mu-sub-header>
        <div class="main">
          <div>
            数量大于等于：
            <span>{{value1| NumFormat}}</span> SHOW
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value1,amount)"
            />
          </div>
        </div>
        <div class="main">
          <div>
            金额大于等于：
            <span>{{value3| NumFormat}}</span> USDT
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value3,quantity_add)"
            />
          </div>
        </div>
      </mu-list>
      <mu-dialog
        title
        width="600"
        max-width="80%"
        :esc-press-close="false"
        :overlay-close="false"
        :open.sync="openAlert"
      >
        {{name_label}}
        <!-- 一直涨设置提醒阈值 -->
        <mu-text-field type="number" v-model="value2" placeholder></mu-text-field>
        <mu-button slot="actions" flat color="primary" @click="cencle(value2)">取消</mu-button>
        <mu-button slot="actions" flat color="primary" @click="save(value2)">保存设置</mu-button>
      </mu-dialog>
    </mu-paper>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name_label: "",
      active1: 0,
      disabled: true,
      openAlert: false,
      readonly: true,
      value1: "",
      value2: "",
      value3: "",
      value4: "",
      value5: "",
      value6: "",
      value7: "",
      value8: "",
      value1_out: "",
      value2_out: "",
      value3_out: "",
      value5_out: "",
      value6_out: "",
      value8_out: "",
      amount: "amount",
      quantity: "quantity",
      amount_add: "amount_add",
      quantity_add: "quantity_add",
      time: "time",
      flag: "",
      visibility: false,
      events: false,
      calls: false,
      messages: false,
      notifications: false,
      sounds: false,
      videoSounds: false
    };
  },
  mounted() {
    this.get_fetch();
  },
  methods: {
    tab(active1) {
      console.log(this.active1);
      this.get_fetch();
    },
    openAlertDialog(q, t) {
      console.log(q + "q-----------------------");
      console.log(t + "t-----------------------");
      //q = this.value2;
      if(t == "amount"){
        this.name_label = "数量大于等于："
      }else if(t == "quantity_add"){
        this.name_label = "金额大于等于："
      }
      this.value2 = "";
      this.value2_out = "";
      this.flag = t;
      this.openAlert = true;
    },
    cencle(e) {
      console.log("取消");
      this.openAlert = false;
    },
    save(q1) {
      if (this.flag == "amount") {
        console.log("amount");
        var push_data = {
          name: "show_chain_tx_alarm",
          data: {
            amount: this.value3, //充值金额
            volume: this.value2 //数量增加
          }
        };
      } else if (this.flag == "quantity_add") {
        console.log("quantity_add");
        var push_data = {
          name: "show_chain_tx_alarm",
          data: {
            amount: this.value2, //充值金额
            volume: this.value1 //数量增加
          }
        };
      }
      this.fetch(push_data);
      this.openAlert = false;
    },
    fetch(push_data) {
      console.log(push_data);
      var that = this;
      this.$http
        .post(
          "http://trendapi.show-fund.com/update_app_common_config/",
          push_data
        )
        .then(function(res) {
          console.log(res);
          if (res.statusText == "OK") {
            that.get_fetch();
          }
        });
    },
    get_fetch() {
      var that = this;
      this.$http
        .get(
          "http://trendapi.show-fund.com/get_app_common_config/show_chain_tx_alarm/"
        )
        .then(function(res) {
          that.value1 = res.data.data.volume; //数量增加
          that.value3 = res.data.data.amount; //充值金额
        });
    }
  }
};
</script>
<style scoped>
.all {
    font-size: 20px;
}
.main {
  padding: 0px 16px;
  text-align: left;
}
.demo-list-wrap {
  width: 100%;
  overflow: hidden;
}
</style>



